.activeButton {
    background: linear-gradient(90deg, #1134b5 0%, #2353e0 75%) !important;
    color: #f3f7fa !important;
    border: 0 !important;
}

.content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

.content > button {
    margin-top: 0px;
}

.priorityHigh {
    padding: 10px;
    border-radius: 5px;
    background: red;
    color: #ffffff;
}

.priorityMiddle {
    padding: 10px;
    border-radius: 5px;
    background: yellow;
    color: #000;
}

.priorityLow {
    padding: 10px;
    border-radius: 5px;
    background: green;
    color: #ffffff;
}